@font-face {
  font-family: "GothamRegular";
  src: url(../../fonts/GothamRegular.otf);
  font-display: swap;
}
@font-face {
  font-family: "GothamLight";
  src: url(../../fonts/GothamLight.otf);
  font-display: swap;
}
/** ------ Media Queries ------ **/
html {
  overflow-x: hidden;
}

body {
  font-size: 16px;
}

.d-block, picture {
  display: block;
}

.w-100, .wh-100 {
  width: 100%;
}

.h-100, .wh-100 {
  height: 100%;
}

img {
  vertical-align: top;
  height: auto;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

a, button, input[type=submit] {
  cursor: pointer;
  transition: all 0.3s ease;
}

main, section, header, footer, aside {
  width: 100%;
}

a {
  text-decoration: none;
}

textarea, button, input, select, option {
  outline: none;
}

video {
  display: block;
  width: 100%;
  aspect-ratio: 16/9;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

main {
  height: 100vh;
  background: black;
}

.header_actions button, .header .ul_menu > li > a {
  text-transform: uppercase;
  color: white;
}

.header {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(20px);
}
@media (min-width: 1540px) {
  .header {
    padding-block: 1.5625vw;
    padding-inline: 9.375vw;
  }
}
.header_container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 0;
}
.header_container a, .header_container button {
  font-family: "GothamLight";
}
@media (min-width: 1540px) {
  .header_logo {
    max-width: 5.8333333333vw;
  }
}
.header ul,
.header li {
  list-style: none;
}
@media (min-width: 1540px) {
  .header .ul_menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 1.875vw;
  }
}
@media (max-width: 1540px) {
  .header .ul_menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 22px;
  }
}
.header .ul_menu > li {
  position: relative;
}
@media (min-width: 1540px) {
  .header .ul_menu > li > a {
    font-size: 0.8333333333vw;
    letter-spacing: 1.6px;
  }
}
@media (max-width: 1540px) {
  .header .ul_menu > li > a {
    font-size: 15px;
  }
}
.header .ul_menu .sub-menu {
  position: absolute;
  top: 100%;
  left: 0;
  opacity: 0;
  pointer-events: none;
}
@media (min-width: 1540px) {
  .header_actions {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
    gap: 1.875vw;
  }
}
@media (max-width: 1540px) {
  .header_actions {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
    gap: 22px;
  }
}
.header_actions button {
  background: none;
  border: none;
}
@media (min-width: 1540px) {
  .header_actions button {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 0.4166666667vw;
    font-size: 0.7291666667vw;
  }
}
@media (max-width: 1540px) {
  .header_actions button {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 5.2px;
    font-size: 13px;
  }
}
@media (min-width: 1540px) {
  .header_actions button svg {
    display: block;
    width: 0.625vw;
    height: 0.625vw;
    aspect-ratio: 1/1;
    object-fit: contain;
    object-position: center;
  }
}
@media (max-width: 1540px) {
  .header_actions button svg {
    display: block;
    width: 11px;
    height: 11px;
    aspect-ratio: 1/1;
    object-fit: contain;
    object-position: center;
  }
}
.header_actions a {
  color: white;
  width: fit-content;
  border: 1px solid white;
  text-transform: uppercase;
}
@media (min-width: 1540px) {
  .header_actions a {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 0.4166666667vw;
    padding: 0.8854166667vw 1.0416666667vw;
    font-size: 0.8854166667vw;
  }
}
@media (max-width: 1540px) {
  .header_actions a {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 5.2px;
    padding: 12px 18px;
    font-size: 15px;
  }
}